/* TestDatabase ???? ??? */
.test-database-container {
  min-height: 100vh;
  background: linear-gradient(135deg, #1a1a2e 0%, #16213e 50%, #0f3460 100%);
  color: #ffffff;
}

/* ?? ?? ?? */
.test-database-container::before {
  content: '';
  position: fixed;
  top: 0;
  left: -50%;
  width: 200%;
  height: 100%;
  background: linear-gradient(45deg, 
    rgba(139, 0, 0, 0.1) 0%, 
    rgba(255, 215, 0, 0.05) 25%, 
    rgba(139, 0, 0, 0.1) 50%, 
    rgba(255, 215, 0, 0.05) 75%, 
    rgba(139, 0, 0, 0.1) 100%);
  transform: skewX(-15deg);
  z-index: -1;
  animation: curtainFloat 20s ease-in-out infinite;
}

.test-database-container::after {
  content: '';
  position: fixed;
  top: 0;
  right: -50%;
  width: 200%;
  height: 100%;
  background: linear-gradient(-45deg, 
    rgba(255, 215, 0, 0.05) 0%, 
    rgba(139, 0, 0, 0.1) 25%, 
    rgba(255, 215, 0, 0.05) 50%, 
    rgba(139, 0, 0, 0.1) 75%, 
    rgba(255, 215, 0, 0.05) 100%);
  transform: skewX(15deg);
  z-index: -1;
  animation: curtainFloat 25s ease-in-out infinite reverse;
}

@keyframes curtainFloat {
  0%, 100% { transform: skewX(-15deg) translateX(0); }
  50% { transform: skewX(-15deg) translateX(20px); }
}

/* ?? ??? */
.test-database-content {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

/* ?? */
.test-database-header {
  background: rgba(139, 0, 0, 0.2);
  border: 2px solid #8B0000;
  border-radius: 20px;
  padding: 30px;
  margin-bottom: 30px;
  backdrop-filter: blur(10px);
}

.header-content {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}

.test-database-title {
  color: #FFD700;
  font-size: 2.5rem;
  font-weight: 700;
  margin: 0;
  text-shadow: 0 0 20px rgba(255, 215, 0, 0.3);
}

.header-buttons {
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.new-test-btn, .clear-history-btn {
  background: linear-gradient(135deg, #8B0000, #A52A2A);
  border: 3px solid #FFD700;
  color: #FFD700;
  padding: 12px 24px;
  border-radius: 25px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: uppercase;
  letter-spacing: 1px;
  box-shadow: 0 4px 15px rgba(139, 0, 0, 0.3);
}

.new-test-btn:hover, .clear-history-btn:hover {
  background: linear-gradient(135deg, #A52A2A, #8B0000);
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(139, 0, 0, 0.4);
}

/* ??? ?? ?? */
.test-results-list {
  display: grid;
  gap: 20px;
  margin-top: 30px;
}

.test-result-item {
  background: rgba(139, 0, 0, 0.15);
  border: 2px solid rgba(139, 0, 0, 0.3);
  border-radius: 16px;
  padding: 24px;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.test-result-item:hover {
  border-color: #FFD700;
  background: rgba(139, 0, 0, 0.25);
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(139, 0, 0, 0.3);
}

.result-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  flex-wrap: wrap;
  gap: 12px;
}

.result-type {
  background: linear-gradient(135deg, #FFD700, #FFA500);
  color: #2C3E50;
  padding: 8px 16px;
  border-radius: 20px;
  font-size: 1.1rem;
  font-weight: 700;
  letter-spacing: 1px;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.1);
}

.result-date {
  color: #9ca3af;
  font-size: 0.9rem;
  font-weight: 500;
}

.result-details {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  gap: 16px;
  margin-bottom: 20px;
}

.detail-item {
  background: rgba(255, 255, 255, 0.05);
  border: 1px solid rgba(255, 255, 255, 0.1);
  border-radius: 12px;
  padding: 16px;
}

.detail-label {
  color: #FFD700;
  font-size: 0.9rem;
  font-weight: 600;
  margin-bottom: 8px;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.detail-value {
  color: #ffffff;
  font-size: 1rem;
  font-weight: 500;
}

.result-actions {
  display: flex;
  gap: 12px;
  flex-wrap: wrap;
}

.action-btn {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  color: #ffffff;
  padding: 8px 16px;
  border-radius: 8px;
  font-size: 0.9rem;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.3s ease;
  backdrop-filter: blur(10px);
}

.action-btn:hover {
  background: rgba(255, 255, 255, 0.2);
  border-color: #FFD700;
  transform: translateY(-1px);
}

/* ? ?? */
.empty-state {
  text-align: center;
  padding: 60px 20px;
  color: #9ca3af;
}

.empty-state h3 {
  color: #FFD700;
  font-size: 1.8rem;
  margin-bottom: 16px;
}

.empty-state p {
  font-size: 1.1rem;
  margin-bottom: 24px;
  line-height: 1.6;
}

/* ?? ?? */
.loading {
  text-align: center;
  padding: 60px 20px;
  color: #FFD700;
}

.loading-spinner {
  border: 3px solid rgba(255, 215, 0, 0.3);
  border-top: 3px solid #FFD700;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  animation: spin 1s linear infinite;
  margin: 0 auto 20px;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* ??? ??? */
@media (max-width: 768px) {
  .test-database-content {
    padding: 15px;
  }
  
  .test-database-title {
    font-size: 2rem;
  }
  
  .header-content {
    flex-direction: column;
    text-align: center;
  }
  
  .result-header {
    flex-direction: column;
    align-items: flex-start;
  }
  
  .result-details {
    grid-template-columns: 1fr;
  }
  
  .result-actions {
    justify-content: center;
  }
}
