/* SearchModal 전체 오버레이 */
.search-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.6);
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding-top: 120px;
  z-index: 1000;
  backdrop-filter: blur(4px);
}

/* 모달 박스 */
.search-modal {
  background: linear-gradient(145deg, #1a1a1a, #2a2a2a);
  border: 2px solid #8B0000;
  border-radius: 16px;
  padding: 20px;
  width: 380px;
  box-shadow: 0 12px 32px rgba(0, 0, 0, 0.5);
  position: relative;
  max-height: 80vh;
  overflow-y: auto;
  animation: modalSlideIn 0.3s ease-out;
}

/* 모달 상단 커튼 라인 */
.search-modal::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 3px;
  background: linear-gradient(90deg, #8B0000, #d4af37, #8B0000);
  border-radius: 16px 16px 0 0;
}

/* 모달 등장 애니메이션 */
@keyframes modalSlideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.95);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

/* 닫기 버튼 */
.close-btn {
  position: absolute;
  top: 15px;
  right: 15px;
  border: none;
  background: linear-gradient(145deg, #8B0000, #a52a2a);
  color: #f5f5f5;
  font-size: 18px;
  cursor: pointer;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  transition: all 0.3s ease;
  border: 1px solid #d4af37;
  display: flex;
  align-items: center;
  justify-content: center;
}

.close-btn:hover {
  background: linear-gradient(145deg, #a52a2a, #8B0000);
  transform: translateY(-2px) scale(1.1);
  box-shadow: 0 4px 12px rgba(139, 0, 0, 0.4);
}

/* 검색창 영역 */
.search-input-area {
  display: flex;
  gap: 10px;
  margin-bottom: 20px;
  margin-top: 10px;
}

.search-input {
  flex: 1;
  padding: 12px 16px;
  border: 2px solid #8B0000;
  border-radius: 10px;
  background: #1a1a1a;
  color: #f5f5f5;
  font-size: 14px;
  transition: all 0.3s ease;
}

.search-input:focus {
  outline: none;
  border-color: #d4af37;
  box-shadow: 0 0 0 3px rgba(212, 175, 55, 0.2);
  background: #2a2a2a;
}

.search-input::placeholder {
  color: #9ca3af;
}

.search-btn {
  padding: 12px 18px;
  border: none;
  background: linear-gradient(145deg, #8B0000, #a52a2a);
  color: #f5f5f5;
  border-radius: 10px;
  cursor: pointer;
  font-weight: 600;
  transition: all 0.3s ease;
  border: 1px solid #d4af37;
  min-width: 60px;
}

.search-btn:hover {
  background: linear-gradient(145deg, #a52a2a, #8B0000);
  transform: translateY(-2px);
  box-shadow: 0 4px 12px rgba(139, 0, 0, 0.4);
}

/* 실검 박스 */
.top-search-box {
  background: linear-gradient(145deg, #2a2a2a, #1a1a1a);
  border: 1px solid #8B0000;
  border-radius: 12px;
  padding: 16px;
  position: relative;
  overflow: hidden;
}

.top-search-box::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: linear-gradient(90deg, #8B0000, #d4af37, #8B0000);
}

.top-search-header {
  font-weight: 700;
  margin-bottom: 12px;
  color: #d4af37;
  font-size: 16px;
  text-align: center;
}

.top-search-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.top-search-list li {
  display: flex;
  align-items: center;
  font-size: 14px;
  padding: 8px 0;
  border-bottom: 1px solid rgba(139, 0, 0, 0.3);
  transition: all 0.3s ease;
  color: #f5f5f5;
}

.top-search-list li:hover {
  background: rgba(139, 0, 0, 0.1);
  padding-left: 8px;
  border-radius: 6px;
}

.top-search-list li:last-child {
  border-bottom: none;
}

.rank {
  font-weight: 700;
  margin-right: 10px;
  color: #d4af37;
  background: rgba(139, 0, 0, 0.2);
  width: 24px;
  height: 24px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 12px;
  border: 1px solid #d4af37;
}

/* 커튼 장식 요소 */
.search-modal .curtain-decoration {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  pointer-events: none;
  z-index: -1;
}

.search-modal .curtain-decoration::before {
  content: '';
  position: absolute;
  top: 20px;
  left: 20px;
  width: 40px;
  height: 40px;
  background: linear-gradient(45deg, 
    rgba(139, 0, 0, 0.1) 0%,
    rgba(212, 175, 55, 0.1) 50%,
    transparent 100%);
  border-radius: 50%;
  animation: curtainFloat 4s ease-in-out infinite;
}

.search-modal .curtain-decoration::after {
  content: '';
  position: absolute;
  bottom: 20px;
  right: 20px;
  width: 30px;
  height: 30px;
  background: linear-gradient(45deg, 
    rgba(212, 175, 55, 0.1) 0%,
    rgba(139, 0, 0, 0.1) 50%,
    transparent 100%);
  border-radius: 50%;
  animation: curtainFloat 4s ease-in-out infinite 2s;
}

@keyframes curtainFloat {
  0%, 100% { transform: translateY(0) scale(1); opacity: 0.1; }
  50% { transform: translateY(-10px) scale(1.2); opacity: 0.3; }
}
